* {
  margin: 0;
}
ul {
  list-style: none;
  padding: 0;
}
a {
  text-decoration: none;
}
button {
  background: none;
  padding: 0;
  cursor: pointer;
}
body {
  font-family: "Roboto";
}
.black-button {
  background: #030305;
  color: #ffffff;
  padding: 20px 0;
  display: flex;
  width: 100%;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
}
